import { Action } from '@ali/4ever-cangjie';
import Table from '../../mo/models';

export const TABLE_SELECT_ROWS = Symbol('TABLE_SELECT_ROWS');

export function selectTableRows(
  table: Table,
  startIndex: number,
  endIndex: number,
) {
  return {
    type: TABLE_SELECT_ROWS,
    payload: {
      node: table,
      startIndex,
      endIndex,
    },
  };
}

interface TableSelectTableRowsPayload {
  node: Table;
  startIndex: number;
  endIndex: number;
}

interface TableSelectTableRowsAction {
  type: typeof TABLE_SELECT_ROWS;
  payload: TableSelectTableRowsPayload;
}

export function isTableSelectTableRowsAction(
  action: Action,
): action is TableSelectTableRowsAction {
  return action.type === TABLE_SELECT_ROWS;
}
